import React ,{Component} from 'react';
import './index.css';
import List from './List';
import axios from 'axios';
import {testPORT} from '../../../api/port';
class Detail extends Component{
    constructor(props){
         super(props);
         this.state={
             listData:[]
         }
    }
    render(){
        let userName=window.localStorage.getItem("name")?JSON.parse(window.localStorage.getItem("name")).name:null;
        let names=(this.props.history.location.state&&this.props.history.location.state.name)||userName;
        let {listData}=this.state;
        return <div>
                {
                names?<div className="choice">{names}</div>:<div className="choice">
                    
                </div>
                }
                <List listData={listData} />
            </div>
    };
    componentDidMount(){
        let userID=window.localStorage.getItem("id")?JSON.parse(window.localStorage.getItem("id")).id:null;
        let id=(this.props.history.location.state&&this.props.history.location.state.id)||userID;
        axios.get(testPORT+"/singleDetail",{params:{id}}).then((res)=>{
            this.setState({listData:res.data.data})
        })
    }
  
}
export default Detail


